import { ActionCallback } from '../../interface';
import Action from './base';

/**  Action */
export default class CallbackAction extends Action {
  /**
   * 
   */
  public callback: ActionCallback;
  /**
   * 
   */
  public execute() {
    if (this.callback) {
      this.callback(this.context);
    }
  }
  /**
   * 
   */
  public destroy() {
    super.destroy();
    this.callback = null;
  }
}
