import Path, { PathCfg } from './path';
/**  Path  ShapeFactory */
import './shape/line';

/**
 * Line 
 * 
 */
export default class Line extends Path {
  public type: string = 'line';

  constructor(cfg: PathCfg) {
    super(cfg);

    const { sortable = false } = cfg; //  X 
    this.sortable = sortable;
  }
}
