import { Point } from '../../../dependents';
import Action from '../base';
import { distance } from '../util';

const DISTANCE = 4; // 

/**
 * @ignore
 * View  Drag  Action
 */
class Drag extends Action {
  // Action  
  protected starting = false;
  // 
  protected dragStart = false;
  // 
  protected startPoint: Point;

  /**
   * 
   */
  public start() {
    this.starting = true;
    this.startPoint = this.context.getCurrentPoint();
  }

  /**
   * 
   */
  public drag() {
    if (!this.startPoint) {
      return;
    }
    const current = this.context.getCurrentPoint();
    const view = this.context.view;
    const event = this.context.event;
    if (!this.dragStart) {
      if (distance(current, this.startPoint) > DISTANCE) {
        view.emit('dragstart', {
          target: event.target,
          x: event.x,
          y: event.y,
        });
        this.dragStart = true;
      }
    } else {
      view.emit('drag', {
        target: event.target,
        x: event.x,
        y: event.y,
      });
    }
  }

  /**
   * 
   */
  public end() {
    if (this.dragStart) {
      const view = this.context.view;
      const event = this.context.event;
      view.emit('dragend', {
        target: event.target,
        x: event.x,
        y: event.y,
      });
    }
    this.starting = false;
    this.dragStart = false;
  }
}

export default Drag;
