import Action from './base';

/**
 *  Action
 * @ignore
 */
class CursorAction extends Action {
  private setCursor(cursor) {
    const view = this.context.view;
    view.getCanvas().setCursor(cursor);
  }

  /**
   * 
   */
  public default() {
    this.setCursor('default');
  }

  /**  */
  public pointer() {
    this.setCursor('pointer');
  }
  /**  */
  public move() {
    this.setCursor('move');
  }
  /**  */
  public crosshair() {
    this.setCursor('crosshair');
  }
  /**  */
  public wait() {
    this.setCursor('wait');
  }

  /**  */
  public help() {
    this.setCursor('help');
  }

  /**  */
  public text() {
    this.setCursor('text');
  }

  /**
   * 
   */
  public eResize() {
    this.setCursor('e-resize');
  }

  /**
   * 
   */
  public wResize() {
    this.setCursor('w-resize');
  }

  /**
   * 
   */
  public nResize() {
    this.setCursor('n-resize');
  }

  /**
   * 
   */
  public sResize() {
    this.setCursor('s-resize');
  }
  /**
   *  
   */
  public neResize() {
    this.setCursor('ne-resize');
  }
  /**
   *  
   */
  public nwResize() {
    this.setCursor('nw-resize');
  }
  /**
   * 
   */
  public seResize() {
    this.setCursor('se-resize');
  }
  /**
   * 
   */
  public swResize() {
    this.setCursor('sw-resize');
  }

  /**
   * 
   */
  public nsResize() {
    this.setCursor('ns-resize');
  }
  /**
   * 
   */
  public ewResize() {
    this.setCursor('ew-resize');
  }
  /**
   * 
   */
  public zoomIn() {
    this.setCursor('zoom-in');
  }
  /**
   * 
   */
  public zoomOut() {
    this.setCursor('zoom-out');
  }
}

export default CursorAction;
