import { IShape } from '../interfaces';
import { LooseObject } from '../types';

class GraphEvent {
  /**
   * 
   * @type {string}
   */
  type: string;
  /**
   * 
   * @type {string}
   */
  name: string;
  /**
   *  x
   * @type {number}
   */
  x: number;
  /**
   *  y
   * @type {number}
   */
  y: number;
  /**
   *  x
   * @type {number}
   */
  clientX: number;
  /**
   *  y
   * @type {number}
   */
  clientY: number;
  /**
   * 
   * @type {boolean}
   */
  bubbles: boolean = true;
  /**
   * 
   * @type {object}
   */
  target: LooseObject = null;
  /**
   * 
   * @type {object}
   */
  currentTarget: LooseObject = null;
  /**
   * 
   * @type {object}
   */
  delegateTarget: LooseObject = null;
  /**
   *  ev.delegateObject = ev.currentTarget.get('delegateObject')
   * @type {object}
   */
  delegateObject: object = null;
  /**
   * 
   * @type {boolean}
   */
  defaultPrevented: boolean = false;
  /**
   * 
   * @type {boolean}
   */
  propagationStopped: boolean = false;
  /**
   * 
   * @type {IShape}
   */
  shape: IShape = null;
  /**
   * 
   * @type {IShape}
   */
  fromShape: IShape = null;
  /**
   * 
   * @type {IShape}
   */
  toShape: IShape = null;

  /**
   * 
   * @type {number}
   */
  timeStamp: number;

  /**
   * 
   * @type {object}
   */
  originalEvent: Event;

  // 
  propagationPath: any[] = [];

  constructor(type, event) {
    this.type = type;
    this.name = type;
    this.originalEvent = event;
    this.timeStamp = event.timeStamp;
  }

  /**
   * 
   */
  preventDefault() {
    this.defaultPrevented = true;
    if (this.originalEvent.preventDefault) {
      this.originalEvent.preventDefault();
    }
  }

  /**
   * 
   */
  stopPropagation() {
    this.propagationStopped = true;
  }

  toString() {
    const type = this.type;
    return `[Event (type=${type})]`;
  }

  save() {}

  restore() {}
}

export default GraphEvent;
