  private onCanvasEvent = (evt: GEvent): void => {
    const name = evt.name;
    if (!name.includes(':')) {
      // 
      const e = this.createViewEvent(evt);
      //  plot 
      this.doPlotEvent(e);
      this.emit(name, e);
    }
  };

