import { distance, getBBoxByArray } from './util';
import * as vec2 from 'gl-matrix/vec2';
import { BBox, Point } from './types';

export default {
  /**
   * 
   * @param {number} x1  x
   * @param {number} y1  y
   * @param {number} x2  x
   * @param {number} y2  y
   * @return {object} 
   */
  box(x1: number, y1: number, x2: number, y2: number): BBox {
    return getBBoxByArray([x1, x2], [y1, y2]);
  },
  /**
   * 
   * @param {number} x1  x
   * @param {number} y1  y
   * @param {number} x2  x
   * @param {number} y2  y
   * @return {number} 
   */
  length(x1: number, y1: number, x2: number, y2: number) {
    return distance(x1, y1, x2, y2);
  },
  /**
   * 
   * @param {number} x1  x
   * @param {number} y1  y
   * @param {number} x2  x
   * @param {number} y2  y
   * @param {number} t 
   * @return {object}  x, y 
   */
  pointAt(x1: number, y1: number, x2: number, y2: number, t: number): Point {
    return {
      x: (1 - t) * x1 + t * x2,
      y: (1 - t) * y1 + t * y2,
    };
  },
  /**
   * 
   * @param {number} x1  x
   * @param {number} y1  y
   * @param {number} x2  x
   * @param {number} y2  y
   * @param {number} x   x
   * @param {number} y   y
   * @return {number} 
   */
  pointDistance(x1: number, y1: number, x2: number, y2: number, x: number, y: number): number {
    //  x1, y1  p, p1, p2  a
    // p1a = p1p.p1p2/|p1p2| * (p1p )
    const cross = (x2 - x1) * (x - x1) + (y2 - y1) * (y - y1);
    if (cross < 0) {
      return distance(x1, y1, x, y);
    }
    const lengthSquare = (x2 - x1) * (x2 - x1) + (y2 - y1) * (y2 - y1);
    if (cross > lengthSquare) {
      return distance(x2, y2, x, y);
    }
    return this.pointToLine(x1, y1, x2, y2, x, y);
  },
  /**
   * 
   * @param {number} x1  x
   * @param {number} y1  y
   * @param {number} x2  x
   * @param {number} y2  y
   * @param {number} x   x
   * @param {number} y   y
   * @return {number} 
   */
  pointToLine(x1: number, y1: number, x2: number, y2: number, x: number, y: number) {
    const d = [x2 - x1, y2 - y1];
    // 
    if (vec2.exactEquals(d, [0, 0])) {
      return Math.sqrt((x - x1) * (x - x1) + (y - y1) * (y - y1));
    }
    const u = [-d[1], d[0]];
    vec2.normalize(u, u);
    const a = [x - x1, y - y1];
    return Math.abs(vec2.dot(a, u));
  },
  /**
   * 
   * @param {number} x1  x
   * @param {number} y1  y
   * @param {number} x2  x
   * @param {number} y2  y
   * @return {number} 
   */
  tangentAngle(x1: number, y1: number, x2: number, y2: number) {
    return Math.atan2(y2 - y1, x2 - x1);
  },
};
